.sr-only { position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip: rect(0, 0, 0, 0); border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus { position: static; width: auto; height: auto; margin: 0; overflow: visible; clip: auto; }

.joyride-beacon { appearance: none; background-color: transparent; border: 0; cursor: pointer; display: inline-block; line-height: 1; padding: 0; height: 36px; position: relative; width: 36px; z-index: 1500; }

.joyride-beacon:active, .joyride-beacon:focus, .joyride-beacon:hover { outline: none; }

.joyride-beacon__inner { animation: joyride-beacon-inner 1.2s infinite ease-in-out; background-color: #f04; border-radius: 50%; display: block; height: 50%; left: 50%; opacity: 0.7; position: absolute; top: 50%; transform: translate(-50%, -50%); width: 50%; }

.joyride-beacon__outer { animation: joyride-beacon-outer 1.2s infinite ease-in-out; background-color: rgba(255, 0, 68, 0.2); border: 2px solid #f04; border-radius: 50%; box-sizing: border-box; display: block; height: 100%; left: 0; opacity: 0.9; position: absolute; top: 0; transform: translateY(-50%); transform-origin: center; width: 100%; }

.joyride-overlay { bottom: 0; left: 0; position: absolute; right: 0; top: 0; z-index: 1500; }

.joyride-hole { border-radius: 4px; box-shadow: 0 0 0 9999px rgba(0, 0, 0, 0.5), 0 0 15px rgba(0, 0, 0, 0.5); position: absolute; }

.joyride-hole.safari { box-shadow: 0 0 999px 9999px rgba(0, 0, 0, 0.5), 0 0 15px rgba(0, 0, 0, 0.5); }

.joyride-tooltip { background-color: #fff; border-radius: 4px; color: #555; cursor: default; filter: drop-shadow(-1px -2px 3px rgba(0, 0, 0, 0.3)) drop-shadow(1px 2px 3px rgba(0, 0, 0, 0.3)); opacity: 0; padding: 20px; pointer-events: auto; transform: translate3d(0, 0, 0); width: 290px; z-index: 1510; }

.joyride-tooltip--animate { animation: joyride-tooltip 0.4s forwards; animation-timing-function: cubic-bezier(0, 1.05, 0.55, 1.18); }

.joyride-tooltip__triangle { background-repeat: no-repeat; position: absolute; }

.joyride-tooltip.bottom, .joyride-tooltip.bottom-left, .joyride-tooltip.bottom-right { margin-top: 18px; }

